<template>
  <div class="movie_home_index">
    <div class="movie_left">
      <div class="inner_container">
        <div class="movie_head">
          <span @click="$router.push('/movie')" class="solgan">{{top250.category}}</span>
          <div @click.stop="OnMoreMovie" class="more" data="movies.title">
            <span class="more_span">更多</span>
            <img class="more_img" src="../../assets/images/icon/arrow-right.png" />
          </div>
        </div>
      </div>
      <MovieList class="movieList" :movies="top250" />
    </div>
    <div class="movie_right">there is something later</div>
  </div>
</template>

<script>
import { movieTop5 } from "../../assets/data/movie/movie";
import { converToStarsArray } from "../../utils/utils";
import MovieList from "../../components/movie/movieList";

export default {
  name: "movieHomepage",
  components: {
    MovieList
  },
  data() {
    return {
      top250: {}
    };
  },
  created() {
    this.processDoubanData(movieTop5);
  },
  methods: {
    processDoubanData(data) {
      console.log(data);
      var movies = [];
      for (var idx in data.subjects) {
        var subject = data.subjects[idx];
        var title = subject.title;
        if (title.length >= 6) {
          title = title.substring(0, 6) + "...";
        }
        var temp = {
          stars: converToStarsArray(subject.rating.stars),
          title,
          average: subject.rating.average,
          coverageUrl: subject.images.small,
          id: subject.id
        };
        movies.push(temp);
      }
      this.top250 = {
        movies,
        category: data.title
      };
    },
    OnMoreMovie() {
      this.$notify.warning({
        title: "温馨提示",
        message: "非常抱歉,由于接口的问题,该功能尚未实现",
        duration: 2500
      });
    }
  }
};
</script>

